.mobile-overflow{
    overflow-x: scroll;
    width: 100%;
}
.mobile-overflow .card{
    min-width: 30%;
    position:relative;
}
.spec-tab{
    position: absolute;
    margin: auto;
}
.mobile-overflow .card p{
    color: rgb(255, 123, 0);

}
.mobile-overflow .card p:hover{

    text-decoration: underline;
}
.mobile-overflow .card li{
    list-style: none;

}
.spec-tab{
    background-color: rgb(255, 236, 236);
    font-size: 17px;
    height: 60%;
    max-height: 60%;
}
@media screen and (max-width: 992px) {
    .spec-tab {
      background-color:  rgb(255, 236, 236);
      font-size: 9px;
      text-align: center;
    
    }
  }