
@import url('https://fonts.googleapis.com/css2?family=Paytone+One&display=swap');

body {
background-color: white;
background-size: cover;
background-repeat: no-repeat;
font-size: 20px;
}

.nav{
   
    color: black;
}
.carousel-caption{
    position: absolute;
  left: 50px;
    bottom: 150px;

}
.carousel-item{
    position: relative;
}
.card-1{
    background-color: transparent;
    width: 35%;
    backdrop-filter: blur(5px);
    border: none;
    color:#dedede;
    font-size: 30px;
    border-radius: 30px;
}
.card-2{
    background-color: transparent;
    width: 35%;
    backdrop-filter: blur(5px);
    border: none;
    color:#dedede;
    font-size: 30px;
    border-radius: 30px;
}
.card-3{
    background-color: transparent;
    width: 35%;
    backdrop-filter: blur(5px);
    border: none;
    color:#dedede;
    font-size: 30px;
    border-radius: 30px;
}
.card-1 .sec-heading{
    font-family: 'Gloria Hallelujah', cursive;
    font-size: 45px;
    font-weight: bold;
}

.card-2 button {
    background-color: transparent;
    font-size: 40px;
    border: none;
    margin-top:2% ;
}
.card-3{

 color: black;
}
.card-3 button{
    border: none;
    background-color: transparent;
    margin-top: 10%;
}
.banner{
    background-image: url(./components/body/home/mi/image/banner.webp);
margin-top: 30px;
margin-bottom: 30px;
    background-repeat: no-repeat;
    background-size:cover;
    height: 170px;
}
.card-banner{
    width: 40%;
    background-color: transparent;
    margin-left: 5%;
    border: none;
}
.mi-banner{
    background-image: url(./components/body/home/mi/image/mi\ banner.webp);
background-repeat: no-repeat;
background-size:110%;
border-radius: 10px;

transition: 0.5s;
}
.mi-banner:hover{
    background-image: url(./components/body/home/mi/image/mi\ banner.webp);
background-repeat: no-repeat;
background-size:115%; 
transition: 2s;
border-radius: 10px;
}
a{
    color: #000000;
    text-decoration: none;
}

.mi-banner-card{
    background-color: transparent;
}
.mi-banner-card h1{
    font-size: 50px;
    font-family: 'Paytone One', sans-serif;
}
.realme-grid{
    border-radius: 20px;
}
.realme-grid img{
    border-radius: 20px;
}
.realme-grid img:hover{
    border-radius: 20px;
}
.realme-grid1{
    position: relative;
}
.realme-grid1-card{
    position: absolute;
    background-color: transparent;
    bottom: 20px;
    left: 20px;
    color: #000000;
    border: none;
}
.realme-grid23{
    position: relative;
}
.realme-grid23-card{
    position: absolute;
top:25%;
left: 20px;
    background: transparent;
    font-size: 25px;
    border: none;
}
.footer-top{
    border-bottom:1px solid aliceblue ;
}
.footer li{
list-style: none;
font-size: 15px;
transition: 0.2s;
display: block;
}
.footer li:hover{
list-style: none;
font-size: 15px;
color: #ff0000;
transition: 0.5s;
}
.footer-full-scrn{
    display: flex;
}
.footer-small{
    display: none;
}
.copyrights p{
border-right: 1px solid aliceblue;
padding: 5px;
line-height: 1;
align-items: center;
}
.copyrights p:hover{
border-right: 1px solid aliceblue;
padding: 5px;
color: #ff0000;
transition: 0.5s;
line-height: 1;
align-items: center;
}
@media only screen and (max-width: 600px) {
    .mi-banner-card h1{
        font-size: 20px;
        font-family: 'Paytone One', sans-serif;
        margin-top: 5px;
    }
    .mi-banner-card h3{
        font-size: 20px;
        
    }
    .realme-grid1{
        position: relative;
    }
    .realme-grid1-card{
        position: absolute;
        background-color: transparent;
   
        bottom: 0px;
        left: 15px;
     color: #000000;    
    }

    .realme-grid1-card h1{
        font-size: 20px;
    }
    .realme-grid1-card h3{
        font-size: 20px;
    }
    .copyrights{
        font-size: 10px;
    }

}
@media only screen and (max-width: 1000px){
    .footer-full-scrn{
        display: none;
    }
    .footer-small{
        display: block;
    }

}