@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css");
.support-heading{
    font-family:sans-serif;
    padding: 20px;
    border-bottom: 2px solid;
}
.repair-img{
  width: 100%;
  transition: transform 0.3s ease-in-out ;
}
.repair-img:hover{
  width: 100%;
  transform: scale(1.1);
}
.repair-banner{
    margin-top: 30px;
    padding: 10px ;
    border-bottom: 2px solid;
}
i{
  
  transition: 0.5s;

}
i:hover{
    font-size: 70px;
    transition: 0.5s;
    color: red;
}
.open-service{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.support-banner{
  background-image: url('./img/open-service-enter.png');
  height: 350px;
  background-size: 100%;
  transition: 0.5s;
} 
.support-banner:hover{
  background-image: url('./img/open-service-enter.png');
  height: 350px;
  background-size: 110%;
  transition: 0.5s;
} 
.contactus-container{
  margin-left: auto;
  margin-right: auto;
  
}
.contact-us{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.contactus-pic{
  background-image: url('./img/email-us.jpg');
  background-size: 100%;
  transition: 0.5s;
  height: 450px;
}
.contactus-pic:hover{
  background-image: url('./img/email-us.jpg');
  background-size: 110%;
  transition: 0.5s;
}
.enquiry{
  flex-direction: column;
}
.enquiry-bg{
  background-image: url('./img/contact-us.jpg');
  background-size: 100%;
  background-repeat: no-repeat;
  transition: 0.5s;
}
.enquiry-bg:hover{
  background-image: url('./img/contact-us.jpg');
  background-size: 115%;
  background-repeat: no-repeat;
  transition: 0.5s;
}