.banner-community{
    height: 320px;
    background-image: url('/src/components/body/community/img/banner.jpg');
    background-repeat: no-repeat;
    background-size: 100%;
    transition: 1s;
    margin-top: 20px;
    margin-bottom: 20px;
}
.banner-community:hover{
    height: 320px;
    background-image: url('/src/components/body/community/img/banner.jpg');
    background-repeat: no-repeat;
    background-size: 110%;
    transition: 1s;
}
.banner-card{
    background: none;
    color: aliceblue;
    backdrop-filter: blur(10px);
}
.card-profile{
    width: 40px;
    height: 40px;
    border-radius: 50px;
}